@import '../../assets/styles/Utils';

.Main {
  width: 100vw;
  height: auto;
  background: #001c00;
  color: #fff;
  // overflow: auto;
}

.Content {
  width: 100vw;
  height: a;
  box-sizing: border-box;
  min-height: pw(900);
  z-index: 9;
  position: relative;
}

.TransitionContainer {
  // position: absolute;
  width: 100%;
  height: auto;
  box-sizing: border-box;
  // padding: 0 pw(240) pw(30);
}

.Header {
  width: 100%;
  padding: 0 pw(240);
  position: relative;
  z-index: 99;

  .ant-dropdown-menu {
    background: none !important;
  }
}

.HeaderContainer {
  @include flex(space-between);

  width: 100%;
  height: 100%;
  border-bottom: 1px solid #2e442d;
  padding: pw(40) 0 pw(40);
}

.HeaderLeft {
  width: pw(212);
  height: pw(39);
  cursor: pointer;

  img {
    width: 100%;
    height: 100%;
  }
}

.HeaderRight {
  position: relative;
}

.HeaderRightNav {
  min-width: pw(42);
  min-height: pw(42);
}

.Hamburger {
  width: pw(42);
  height: pw(42);
  cursor: pointer;
}

.HamburgerNav {
  position: absolute;
  top: pw(120);
  right: pw(240);
  // width: pw(200);
  z-index: 99;
  background: rgba($color: #000000, $alpha: 0.3);
}

.HamburgerNavItem {
  @include flex(flex-end);

  margin-bottom: pw(30);
  font-weight: 400;
  font-size: pw(16);
  cursor: pointer;
  color: #fff;
}

.HamburgerNavItemLine {
  width: pw(30);
  height: pw(6);
  background: #3f9a55;
  margin-left: pw(10);
}

.Footer {
  width: 100%;
  padding: 0 pw(240);
  position: relative;
  z-index: 9;
}

.FooterContainer {
  @include flex(space-between);

  width: 100%;
  height: 100%;
  border-top: 1px solid #2e442d;
  padding: pw(40) 0 pw(40);
}

.FooterNav {
  @include flex(flex-start);
}

.FooterCopyright {
  font-weight: 400;
  font-size: pw(14);
  color: #ffffff;
  opacity: 0.5;
  margin-top: pw(20);
}

.FooterNavItem {
  margin-right: pw(40);
  font-weight: 400;
  font-size: pw(15);
  cursor: pointer;
}

.fadeContainer {
  position: relative;
}

.fade-enter {
  width: 100%;
  opacity: 0;
  z-index: 1;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 250ms ease-out, transform 300ms ease;
}

.fade-exit {
  width: 100%;
  opacity: 1;
}

.fade-exit-active {
  opacity: 0;
  transition: opacity 250ms ease-out, transform 300ms ease;
}

@media (max-width: 768px) {
  .Header {
    padding: 0 15px;
  }

  .FooterNav {
    display: none;
  }

  .Hamburger {
    width: 32px;
    height: 32px;
  }

  .HeaderLeft {
    width: auto;
    height: 32px;
  }

  .HamburgerNav {
    width: pw(1000);
    top: 42px;
    right: 15px;
  }

  .HeaderCenterNav {
    display: none !important;
  }

  .Header {
    & * {
      font-size: 14px !important;
    }
  }
}

@media (min-width: 769px) {
  .Hamburger {
    display: none;
  }
}

.HeaderRightNav {
  @include flex(flex-end);
}

// .HamburgerNav {
//   display: none;
// }

.HeaderCenterNav {
  @include flex(space-between);

  font-weight: 600;
  font-size: pw(17);
}

.HeaderNavItem {
  margin: 0 pw(20);
  cursor: pointer;
  border-bottom: 4px solid transparent;
  border-top: 4px solid transparent;
}

.MenuActive {
  border-bottom: 4px solid #3f9a55;
}

.Background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: 0;
  background: #001c00;
}

.BackgroundVideo {
  width: 100vw;
  height: 100vh;
  object-fit: fill;
}
