@import '../../../../assets/styles/Utils.scss';

.Volunteer {
  @include flex;

  flex-direction: column;
  padding: 0 pw(360) pw(309);
}

.Card {
  @include flex(flex-start, center);
  flex-direction: column;
  // width: pw(418);
  // height: pw(386);
  border: 1px solid #3f9a55;
  border-radius: pw(20);
  margin-top: pw(80);
  position: relative;
  padding: 0 pw(90) pw(20);
}

.CardLine {
  position: absolute;
  top: 0;
}

.CardHead {
  @include flex;
  margin-top: pw(40);
  width: pw(145);
  height: pw(145);
  position: relative;
  z-index: 1;

  background: #051b00;
  border: 2px solid #ffdc30;
  border-radius: 50%;
}

.CardHeadImage {
  width: pw(96);
  height: pw(96);
}

.CardLine {
  width: pw(100);
  height: pw(62);

  img {
    width: 100%;
    height: 100%;
  }
}

.Text {
  font-weight: 400;
  font-size: pw(16);
  text-align: center;
  color: #5ae37b;
  opacity: 0.7;
  margin-top: pw(20);
}

.Title {
  font-style: normal;
  font-weight: 900;
  font-size: pw(36);
  text-transform: capitalize;
  color: #5ae37b;
  margin: pw(15) 0;
}

.Button {
  margin-bottom: pw(25);
}

.CardDesc {
  font-weight: 400;
  font-size: pw(16);
  text-align: center;
  color: #5ae37b;
  margin-bottom: pw(280);
  margin-top: pw(60);
  width: pw(732);
}

@media (max-width: 768px) {
  .Volunteer {
    & * {
      font-size: 14px !important;
    }
  }

  .CardDesc {
    width: 100%;
  }
}
