@import '../../../../assets/styles/Utils.scss';

.Roadmap {
  width: pw(650);
  position: relative;
  // padding: 0 pw(120) pw(320);
  margin-left: pw(360);
}

.RoadmapBg {
  background-image: url(../../../../assets/images/Roadmap3.png);
  position: absolute;
  width: 100vw;
  height: 100vh;
  left: 0;
  top: pw(-120);
  background-size: 100vw 100vh;
  z-index: 9;
}

.Roadmap1 {
  position: absolute;
  width: pw(550);
  height: pw(550);
  right: pw(120);
  top: pw(220);
  background-size: 100vw 100vh;
  z-index: 9;
}

.Roadmap2 {
  position: absolute;
  width: pw(550);
  height: pw(550);
  right: pw(120);
  top: pw(920);
  background-size: 100vw 100vh;
  z-index: 9;
}

.RoadmapTitle {
  font-weight: 900;
  font-size: pw(48);
  text-align: left;
  margin-top: pw(160);
  margin-bottom: pw(30);
}

.RoadmapItem {
  width: pw(360);
  height: auto;
  background: #0a2809;
  border: 2px solid #3f9a55;
  border-radius: 20px;
  padding: pw(30);
  margin-bottom: pw(40);
  position: relative;
  z-index: 1;
}

.RoadmapItemIconContainer {
  @include flex(flex-end);
  margin-bottom: pw(34);
}

.RoadmapContainerStart {
  @include flex(flex-start);
}

.RoadmapContainerEnd {
  @include flex(flex-end);
}

.RoadmapItemTime {
  font-style: normal;
  font-weight: 900;
  font-size: pw(18);
}

.RoadmapItemContent {
  font-weight: 400;
  font-size: pw(16);
  color: #ffffff;
  opacity: 0.7;
}

.ListLine {
  width: 1px;
  height: pw(1350);
  position: absolute;
  left: pw(320);
  border-left: 1px dashed #3f9a55;
}

@media (max-width: 768px) {
  .Roadmap {
    width: 100%;
    margin-left: 0;
    z-index: 99;
    padding: 0 15px;
  }

  .RoadmapItem {
    width: 100%;
  }

  .ListLine {
    display: none;
  }
}
