@import '../../../../assets/styles/Utils.scss';

.Resources {
  padding: pw(160) pw(120);
  box-sizing: border-box;
}

.ResourcesTitle {
  font-weight: 900;
  font-size: pw(48);
  color: #ffffff;
}

.ResourcesContent {
  font-weight: 400;
  font-size: pw(18);
  text-align: left;
  opacity: 0.7;
  margin-bottom: pw(40);
}

.Card {
  @include flex(space-between);
  flex-wrap: wrap;
}

.CardItemLogo {
  width: 100%;
  height: auto;
}

.CardItem {
  width: 32%;
  height: auto;
  border-radius: 20px;
  background: #0a2809;
  cursor: pointer;

  &:hover {
    background: radial-gradient(
      139.76% 139.76% at 50% -39.76%,
      #020f1c 0%,
      rgba(63, 154, 85, 0.49) 100%
    );
  }
}

.CardItemContentContainer {
  width: 100%;
  padding: pw(20) pw(30);
  min-height: pw(230);
}

.CardItemTitle {
  font-weight: 900;
  font-size: pw(16);
  text-transform: capitalize;
  color: #ffffff;
  word-break: break-all;
  margin-bottom: pw(10);
}

.CardItemContent {
  font-weight: 400;
  font-size: pw(14);
  color: #ffffff;
  opacity: 0.7;
}

.ResourcesSecTitle {
  font-style: normal;
  font-weight: 900;
  font-size: pw(24);

  color: #ffffff;
  margin-top: pw(100);
}

.ResourcesIcon {
  @include flex(flex-start);
  flex-wrap: wrap;
}

.ResourcesIconItem {
  width: pw(46);
  height: pw(46);
  margin-right: pw(8);
  position: relative;
}

.ResourcesIconImage {
  width: 100%;
  height: 100%;
}

.ResourcesIconItemContainer {
  transition: all 0.5s ease;
  cursor: pointer;
  border-radius: 50%;
  border: 1px solid transparent;

  &:hover {
    transform: scale(1.3) rotate(30deg);
    border: 1px solid #3f9a55;
  }
}

@media (max-width: 479px) {
  .CardItem {
    width: 100%;
    padding: pw(60);
    margin-bottom: pw(40);
  }

  .ResourcesIconItem {
    width: pw(146);
    height: pw(146);
    margin-right: pw(8);
    position: relative;
  }
}
