@import '../../../assets/styles/Utils';

.Button {
  @include flex;

  // width: pw(200);
  // height: pw(50);
  padding: pw(10) pw(60);
  background: #3f9a55;
  border-radius: 4px;
  cursor: pointer;
  font-weight: 900;
  font-size: pw(18);
  color: #ffffff;

  &:hover {
    background: rgba($color: #3f9a55, $alpha: 0.9);
  }

  &:active {
    background: rgba($color: #3f9a55, $alpha: 0.7);
  }
}
