@import '../../../../assets/styles/Utils.scss';

.Team {
  padding: pw(120) pw(120) pw(320);
}

.TeamItem {
  @include flex(flex-start);

  position: relative;
}

.TeamItemContainer {
  margin-top: pw(100);
}

.TeamItemContainerStart {
  @include flex(flex-start);
}

.TeamItemContainerEnd {
  @include flex(flex-end);
}

.TeamItemHead {
  @include flex;

  width: pw(280);
  height: pw(280);
  background-size: 100% 100%;
  margin-right: pw(40);
  position: relative;
  // background-image: url(../../../../assets/images/head_bg.png);
}

.TeamItemHeadLeft {
  &::before {
    content: '';

    position: absolute;
    width: pw(500);
    height: pw(500);
    background: #0d8759;
    left: pw(-420);
    transform: rotate(45deg);
  }
}

.TeamItemHeadRight {
  &::before {
    content: '';

    position: absolute;
    width: pw(500);
    height: pw(500);
    background: #0d8759;
    right: pw(-420);
    transform: rotate(45deg);
  }
}

.TeamItemHeadImage {
  width: 60%;
  height: 60%;
  position: absolute;
  z-index: 9;
}

.TeamItemHeadImageBg {
  width: 100%;
  height: 100%;
  z-index: 3;
  left: 0;
  top: 0;
}

.TeamItemInfo {
  width: pw(413);
}

.Name {
  font-weight: 900;
  font-size: pw(24);
  text-transform: capitalize;
  color: #ffffff;
}

.Post {
  font-weight: 400;
  font-size: pw(18);
  text-transform: capitalize;
  color: #ffffff;
  margin: pw(20) 0;
}

.Desc {
  font-weight: 400;
  font-size: pw(16);
  text-align: justify;
  color: #ffffff;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .Team {
    padding: pw(120) 0 pw(320);
  }

  .TeamItemHead {
    @include flex;

    width: pw(480);
    height: pw(480);
  }

  .TeamItemInfo {
    width: pw(913);
  }

  // .TeamItemContainer {
  //   width: 350px;
  // }

  .Team {
    & * {
      font-size: 14px !important;
    }
  }
}
