@import '../../../../assets/styles/Utils.scss';

.Ecosystem {
  @include flex;
  flex-direction: column;
  padding: 0 pw(360) pw(30);
}

.EcosystemImage {
  width: pw(780);
  height: pw(780);
  margin-top: pw(100);
}

.EcosystemText {
  font-weight: 400;
  font-size: pw(18);
  color: #90e013;
  opacity: 0.7;
  margin: pw(60) 0;
}

.List {
  @include flex(space-between);

  width: 100%;
  height: auto;
}

.ListLeft {
  width: 46%;
}

.ListRight {
  width: 46%;
  padding-top: pw(114);
}

.ListLine {
  width: 1px;
  height: 100%;
  background: #3f9a55;
}

.Item {
  // width: pw(500);
  text-align: left;
  padding: pw(80) 0;
  border-top: 1px solid #3f9a55;
}

.ItemTitle {
  font-weight: 900;
  font-size: pw(36);
  text-transform: capitalize;
  color: #3f9a55;
}

.ItemSecTitle {
  font-weight: 900;
  font-size: pw(24);
  text-transform: capitalize;
  margin-bottom: pw(10);
}

.ItemContent {
  font-weight: 400;
  font-size: pw(18);
  color: #ffffff;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .Ecosystem {
    padding: 0 15px pw(30);
  }

  .Ecosystem {
    & * {
      font-size: 14px !important;
    }
  }
}
