@import '../../../../assets/styles/Utils.scss';

.Index {
  @include flex();

  flex-direction: column;
  text-align: center;
  padding: pw(80) pw(360) pw(309);
}

.Image {
  width: pw(393);
  height: pw(323);
  margin-bottom: pw(60);
}

.Title {
  font-weight: 900;
  font-size: pw(48);
}

.Text {
  width: pw(900);
  font-weight: 400;
  font-size: pw(18);
  margin-bottom: pw(40);
  opacity: 0.7;
}

.Button {
}

.Protocol {
  padding-top: pw(179);
}

.ProtocolImage {
  width: pw(310);
  height: pw(156);
  margin-bottom: pw(46);
}

.ProtocolTitle {
  font-weight: 900;
  font-size: pw(38);
}

.ProtocolText {
  width: pw(732);
  font-weight: 400;
  font-size: pw(18);
  color: #ffffff;
  opacity: 0.7;
  margin-bottom: pw(43);
}

.GreenText {
  color: #009e4c;
}

.Mission {
  width: pw(1000);
  min-height: pw(300);
  background-color: #5cbc47;
  border-radius: pw(36);
  background-image: url(../../../../assets/images/solution_bg_3.png);
  background-size: 100% 100%;
  text-align: left;
  padding: pw(38) pw(45);
  margin-top: pw(100);
  background-repeat: no-repeat;
}

.MissionText {
  width: pw(308);
  color: #ffffff;
  opacity: 0.7;
  font-size: pw(16);
}

.Humans {
  width: pw(1000);
  //min-height: pw(630);
  background: #002a00;
  background-blend-mode: normal, screen;
  border-radius: pw(36);
  text-align: left;
  margin-top: pw(62);
  padding: pw(76) pw(48);
  position: relative;
}

.HumansDesc {
  font-style: normal;
  font-weight: 900;
  font-size: pw(22);
  color: #ffffff;

  opacity: 0.5;
}

.HumansText {
  width: pw(492);
  margin-top: pw(62);
  font-weight: 400;
  font-size: pw(18);
  color: #ffffff;
  opacity: 0.7;
  position: relative;
  z-index: 1;
}

.SolutionBg4 {
  position: absolute;
  width: pw(518 * 0.7);
  height: pw(482 * 0.7);
  background-blend-mode: screen;
  background-image: url(../../../../assets/images/solution_bg_4.png);
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.SDK {
  width: pw(1000);
  text-align: left;
  margin-top: pw(62);
  position: relative;

  .SDKDesc {
    font-size: pw(16);
    color: #5ae37b;
  }

  .SDKImg {
    width: 100%;
  }
}

@media (max-width: 768px) {
  .Text,
  .Mission,
  .Humans,
  .HumansText,
  .MissionText,
  .SDK,
  .ProtocolText {
    width: 100%;
  }

  .Index {
    padding: pw(80) 15px pw(309);
  }

  .Index {
    & * {
      font-size: 14px !important;
    }
  }
}
